import { useEffect, useRef, useState, useCallback } from "react";
import { loadWasm, WasmExports } from "wasmLoader";
import { debounce } from 'lodash';

const NUMBER_OF_SNOWFLAKES = 300;
const SNOWFLAKE_FIELDS = 6; // ÎààÏÜ°Ïù¥ Íµ¨Ï°∞Ï≤¥Ïùò ÌïÑÎìú Í∞úÏàò

const Snowfall = () => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const [wasm, setWasm] = useState<WasmExports | null>(null);

  const handleClientResize = useCallback(
    debounce(() => {
      if (canvasRef.current) {
        const width = document.documentElement.clientWidth;
        const height = document.documentElement.clientHeight;
    
        canvasRef.current.width = width;
        canvasRef.current.height = height;
      }
    }, 200),
    [canvasRef]
  );

  useEffect(() => {
    loadWasm().then((wasm) => {
      if (wasm) {
        setWasm(wasm);
      }
    });
  }, []);

  useEffect(() => {
    if (!wasm) {
      return;
    }

    const canvas = canvasRef.current;
    if (canvas) {
      const ctx = canvas.getContext("2d");
      const width = document.documentElement.clientWidth;
      const height = document.documentElement.clientHeight;

      canvas.width = width;
      canvas.height = height;

      wasm.init_snowflakes(width, height);

      // * wasmÍ∞Ä Ï†ÄÏû•Îêú Î©îÎ™®Î¶¨ Í≥µÍ∞ÑÏúºÎ°úÎ∂ÄÌÑ∞ ÎààÏÜ°Ïù¥ Ï†ïÎ≥¥Î•º ÏùΩÏñ¥ÏÑú Float32Array(4 byte Ïã§ÏàòÎ•º Ï†ÄÏû•ÌïòÎäî Î∞∞Ïó¥)Î°ú Î≥ÄÌôò
      const snowflakes = new Float32Array( // Ïà´Ïûê ÌïòÎÇò Îãπ 4 byte
        wasm.memory.buffer, // Î≤ÑÌçº(wasmÍ∞Ä ÏÇ¨Ïö©ÌïòÎäî Î©îÎ™®Î¶¨ Í≥µÍ∞Ñ Ï†ÑÏ≤¥) - Emscripten Í∏∞Î≥∏Í∞íÏùÄ 16MB(256 ÌéòÏù¥ÏßÄ, byte length=16908288)
        wasm.get_snowflakes(), // Î≤ÑÌçºÏùò Ïñ¥Îäê ÏßÄÏ†êÎ∂ÄÌÑ∞ Îç∞Ïù¥ÌÑ∞Î•º ÏùΩÏùÑÏßÄ Ï†ÑÎã¨
        NUMBER_OF_SNOWFLAKES * SNOWFLAKE_FIELDS, // ÏùΩÏñ¥Ïò¨ ÏöîÏÜåÏùò Í∞úÏàò (ÎààÏÜ°Ïù¥ 100Í∞ú * ÎààÏÜ°Ïù¥ Íµ¨Ï°∞Ï≤¥ ÌïÑÎìú Í∞úÏàò -> Ï¥ù 1200 byte)
      );
      // * Ï∞∏Í≥†:
      // https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Global_Objects/TypedArray#buffer

      const render = () => {
        wasm.update_snowflakes(canvas.width, canvas.height);
        
        if (ctx) {
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          ctx.fillStyle = "rgba(0, 0, 0, 0.1)";

          for (let i = 0; i < snowflakes.length; i += SNOWFLAKE_FIELDS) {
            const gradient = ctx.createRadialGradient(
              snowflakes[i],
              snowflakes[i + 1],
              0,
              snowflakes[i],
              snowflakes[i + 1],
              snowflakes[i + 5],
            );

            gradient.addColorStop(
              0,
              `rgba(255, 255, 255, ${snowflakes[i + 4]})`
            );
            gradient.addColorStop(
              0,
              `rgba(210, 236, 242, ${snowflakes[i + 4]})`
            );
            gradient.addColorStop(
              0,
              `rgba(237, 247, 249, ${snowflakes[i + 4]})`
            );

            ctx.fillStyle = gradient;
            ctx.beginPath();
            ctx.arc(snowflakes[i], snowflakes[i + 1], snowflakes[i + 5], 0, Math.PI * 2);
            ctx.fill();

            ctx.shadowOffsetX = 0;
            ctx.shadowOffsetY = 0;
            ctx.shadowColor = "#fafafa60";
            ctx.shadowBlur = 7;
          }
        }

        requestAnimationFrame(render); // Ïï†ÎãàÎ©îÏù¥ÏÖòÏùÑ Î∂ÄÎìúÎüΩÍ≤å Íµ¨ÌòÑÌïòÍ∏∞ ÏúÑÌï®. - cf.) setInterval()Í≥º Îã¨Î¶¨ ÌÉ≠Ïù¥ ÎπÑÌôúÏÑ±ÌôîÎêòÎ©¥ Î©àÏ∂îÎØÄÎ°ú CPU ÏûêÏõê Ï†àÏïΩ
        // * Ï∞∏Í≥†:
        // https://developer.mozilla.org/ko/docs/Web/API/Window/requestAnimationFrame
      };

      render();
    }
  }, [wasm]);

  useEffect(() => {
    window.addEventListener('resize', handleClientResize);

    return () => {
      window.removeEventListener('resize', handleClientResize);
    };
  }, []);

  return <canvas ref={canvasRef} style={{ position: "fixed", top: 0, right: 0, bottom: 0, left: 0, width: "100vw", height: "100vh", pointerEvents: "none"  }} />
};

export default Snowfall;