export interface WasmExports {
  /** ÎààÏÜ°Ïù¥Îì§ ÏÉùÏÑ± */
  init_snowflakes: (width: number, height: number) => void;
  /** ÎààÏÜ°Ïù¥Îì§ Ï¢åÌëú Î≥ÄÍ≤Ω */
  update_snowflakes: (width: number, height: number) => void;
  /** ÎààÏÜ°Ïù¥Îì§ Îç∞Ïù¥ÌÑ∞Í∞Ä Ï†ÄÏû•Îêú Î©îÎ™®Î¶¨Ïùò ÏãúÏûë ÏúÑÏπò(byte Îã®ÏúÑ)Î•º Î∞òÌôò */
  get_snowflakes: () => number;
  memory: Float32Array;
}

export async function loadWasm() {
  console.log('loadWasm Ìò∏Ï∂ú');
  try {
    const response = await fetch("/wasm/snowflakes.wasm"); // WASM ÌååÏùº Í∞ÄÏ†∏Ïò§Í∏∞
    const bytes = await response.arrayBuffer();
    const { instance } = await WebAssembly.instantiate(bytes, {});

    return instance.exports as unknown as WasmExports; // ÌÉÄÏûÖ Ï∫êÏä§ÌåÖ
  }
  catch (error) {
    console.error("Failed to load WASM: ", error);
    return null;
  }
}